'use client';

import React, {
  createContext,
  useContext,
  useEffect,
  useState,
  ReactNode,
  useRef,
  useCallback,
  memo,
} from 'react';
import { Session, User } from '@supabase/supabase-js';
import { createBrowserSupabaseClient } from './client';

// üéØ ÏôÑÏ†Ñ Ïø†ÌÇ§ Í∏∞Î∞ò Ïù∏Ï¶ù: ÎÑ§Ìä∏ÏõåÌÅ¨ ÏöîÏ≤≠ ÏóÜÎäî Ï¶âÏãú JWT ÌååÏã±
// ‚úÖ getSession() Ï†úÍ±∞Îê® - ÌÉÄÏûÑÏïÑÏõÉ Î¨∏Ï†ú Ìï¥Í≤∞
// ‚úÖ getUser() Ï†úÍ±∞Îê® - ÎÑ§Ìä∏ÏõåÌÅ¨ ÏßÄÏó∞ ÏôÑÏ†Ñ Ï†úÍ±∞  
// ‚úÖ ÏàúÏàò JWT ÌååÏã± - Ïø†ÌÇ§ÏóêÏÑú ÏßÅÏ†ë ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥ Ï∂îÏ∂ú
// ‚ö° Î°úÎî© ÏãúÍ∞Ñ: 0.1Ï¥à ÎØ∏Îßå (Í∏∞Ï°¥ 5-8Ï¥à ‚Üí Í±∞Ïùò Ï¶âÏãú)
import { extractAvatarFromProvider } from '@/utils/image-utils';
import { UserProfiles } from '@/types/interfaces';
import { handleAuthError } from '@/utils/auth-error-handler';

interface AuthContextType {
  session: Session | null;
  user: User | null;
  userProfile: UserProfiles | null;
  isAuthenticated: boolean;
  isLoading: boolean;
  isInitialized: boolean;
  signOut: () => Promise<void>;
  loadUserProfile: (userId: string) => Promise<UserProfiles | null>;
}

const AuthContext = createContext<AuthContextType | undefined>(undefined);

interface AuthProviderProps {
  children: ReactNode;
}

// Ï†ÑÏó≠ ÏÉÅÌÉú Í¥ÄÎ¶¨Î•º ÏúÑÌïú Ïã±Í∏ÄÌÜ§ Ìå®ÌÑ¥
class AuthStore {
  private static instance: AuthStore | null = null;
  private supabaseClient: any = null;
  private listeners: Set<(state: AuthContextType) => void> = new Set();
  private state: AuthContextType = {
    session: null,
    user: null,
    userProfile: null as UserProfiles | null,
    isAuthenticated: false,
    isLoading: true,
    isInitialized: false,
    signOut: this.signOut.bind(this),
    loadUserProfile: this.loadUserProfile.bind(this),
  };
  private initPromise: Promise<void> | null = null;

  static getInstance(): AuthStore {
    if (!AuthStore.instance) {
      AuthStore.instance = new AuthStore();
    }
    return AuthStore.instance;
  }

  private constructor() {
    if (typeof window !== 'undefined') {
      try {
        console.log('üîÑ [AuthStore] Ï¥àÍ∏∞Ìôî ÏãúÏûë');
        
        // localStorageÏóêÏÑú Supabase Auth ÌÜ†ÌÅ∞ Ï≤¥ÌÅ¨ (ÎèôÏ†Å ÌÇ§ ÌôïÏù∏)
        const checkStoredToken = () => {
          try {
            // Supabase ÌîÑÎ°úÏ†ùÌä∏ URLÏóêÏÑú ÌîÑÎ°úÏ†ùÌä∏ ID Ï∂îÏ∂ú
            const supabaseUrl = process.env.NEXT_PUBLIC_SUPABASE_URL;
            if (!supabaseUrl) return false;
            
            const urlParts = supabaseUrl.split('.');
            const projectId = urlParts[0]?.split('://')[1];
            
            // üç™ Ïø†ÌÇ§ÏóêÏÑú ÌÜ†ÌÅ∞ ÌôïÏù∏ÌïòÎäî Ìï®Ïàò
            const checkCookieToken = (projectId: string) => {
              try {
                const cookies = document.cookie.split(';');
                for (let cookie of cookies) {
                  const [name, value] = cookie.trim().split('=');
                  if (name && name.startsWith(`sb-${projectId}-auth-token`) && value) {
                    console.log(`üç™ [AuthStore] Ïø†ÌÇ§Ïóê ÌÜ†ÌÅ∞ (${name}): ÏûàÏùå`);
                    return true;
                  }
                }
                return false;
              } catch (error) {
                console.warn('‚ö†Ô∏è [AuthStore] Ïø†ÌÇ§ ÌÜ†ÌÅ∞ Ï≤¥ÌÅ¨ Ï§ë Ïò§Î•ò:', error);
                return false;
              }
            };
            
            if (projectId) {
              const authKey = `sb-${projectId}-auth-token`;
              
              // 1Îã®Í≥Ñ: localStorage ÌôïÏù∏
              const hasLocalStorageToken = localStorage.getItem(authKey);
              console.log(`üîç [AuthStore] localStorageÏóê ÌÜ†ÌÅ∞ (${authKey}):`, hasLocalStorageToken ? 'ÏûàÏùå' : 'ÏóÜÏùå');
              
              // 2Îã®Í≥Ñ: Ïø†ÌÇ§ ÌôïÏù∏
              const hasCookieToken = checkCookieToken(projectId);
              
              const hasAnyToken = !!hasLocalStorageToken || hasCookieToken;
              console.log(`üîç [AuthStore] ÌÜ†ÌÅ∞ Ï¥ùÌï©:`, {
                localStorage: !!hasLocalStorageToken,
                cookie: hasCookieToken,
                hasAnyToken
              });
              
              return hasAnyToken;
            }
            
            // ÌîÑÎ°úÏ†ùÌä∏ IDÎ•º Ï∂îÏ∂úÌï† Ïàò ÏóÜÎäî Í≤ΩÏö∞ Î™®Îì† Supabase ÌÇ§ ÌôïÏù∏
            let hasLocalStorage = false;
            for (let i = 0; i < localStorage.length; i++) {
              const key = localStorage.key(i);
              if (key && key.startsWith('sb-') && key.endsWith('-auth-token')) {
                const hasToken = localStorage.getItem(key);
                console.log(`üîç [AuthStore] localStorageÏóê ÌÜ†ÌÅ∞ (${key}):`, hasToken ? 'ÏûàÏùå' : 'ÏóÜÏùå');
                if (hasToken) hasLocalStorage = true;
              }
            }
            
            // ÏùºÎ∞òÏ†ÅÏù∏ Ïø†ÌÇ§ Ìå®ÌÑ¥ ÌôïÏù∏
            let hasCookie = false;
            try {
              const cookies = document.cookie.split(';');
              for (let cookie of cookies) {
                const [name, value] = cookie.trim().split('=');
                if (name && name.startsWith('sb-') && name.includes('auth-token') && value) {
                  console.log(`üç™ [AuthStore] Ïø†ÌÇ§Ïóê ÌÜ†ÌÅ∞ (${name}): ÏûàÏùå`);
                  hasCookie = true;
                  break;
                }
              }
            } catch (error) {
              console.warn('‚ö†Ô∏è [AuthStore] ÏùºÎ∞ò Ïø†ÌÇ§ ÌôïÏù∏ Ï§ë Ïò§Î•ò:', error);
            }
            
            const hasAnyToken = hasLocalStorage || hasCookie;
            console.log(`üîç [AuthStore] Ï†ÑÏ≤¥ ÌÜ†ÌÅ∞ ÏÉÅÌÉú:`, {
              localStorage: hasLocalStorage,
              cookie: hasCookie,
              hasAnyToken
            });
            
            return hasAnyToken;
          } catch (error) {
            console.warn('‚ö†Ô∏è [AuthStore] ÌÜ†ÌÅ∞ Ï≤¥ÌÅ¨ Ï§ë Ïò§Î•ò:', error);
            return false;
          }
        };
        
        const hasStoredToken = checkStoredToken();
        const isLoginPage = window.location.pathname.includes('/login');
        const isCallbackPage = window.location.pathname.includes('/callback');
        
        console.log('üîç [AuthStore] Ï¥àÍ∏∞Ìôî Ïª®ÌÖçÏä§Ìä∏:', {
          hasStoredToken,
          isLoginPage,
          isCallbackPage,
          pathname: window.location.pathname
        });
        
        // ÌôòÍ≤Ω Î≥ÄÏàò ÌôïÏù∏ Î∞è ÏïàÏ†ÑÌïú ÌÅ¥ÎùºÏù¥Ïñ∏Ìä∏ ÏÉùÏÑ±
        if (!process.env.NEXT_PUBLIC_SUPABASE_URL || !process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY) {
          console.error('‚ùå [AuthStore] Supabase ÌôòÍ≤Ω Î≥ÄÏàòÍ∞Ä ÏÑ§Ï†ïÎêòÏßÄ ÏïäÏïòÏäµÎãàÎã§.', {
            hasUrl: !!process.env.NEXT_PUBLIC_SUPABASE_URL,
            hasKey: !!process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY
          });
          
          // ÌôòÍ≤Ω Î≥ÄÏàòÍ∞Ä ÏóÜÏñ¥ÎèÑ Í∏∞Î≥∏ ÏÉÅÌÉúÎ°ú Ï¥àÍ∏∞Ìôî
          this.updateState({
            session: null,
            user: null,
            userProfile: null,
            isAuthenticated: false,
            isLoading: false,
            isInitialized: true,
            signOut: this.signOut.bind(this),
            loadUserProfile: this.loadUserProfile.bind(this),
          });
          return;
        }

        // üöÄ ÏàúÏàò getUser() Í∏∞Î∞ò Îπ†Î•∏ Ïù∏Ï¶ù: getSession ÏôÑÏ†ÑÌûà Ïö∞Ìöå
        if (hasStoredToken) {
          console.log('üöÄ [AuthStore] Ïø†ÌÇ§ ÌÜ†ÌÅ∞ Ï°¥Ïû¨ ‚Üí ÏàúÏàò getUser() Í∏∞Î∞ò Îπ†Î•∏ Ïù∏Ï¶ù Ï≤òÎ¶¨');
          this.supabaseClient = createBrowserSupabaseClient();
          
          // getUser()Î°ú ÏßÅÏ†ë ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥ ÌôïÏù∏ (Îß§Ïö∞ Îπ†Î•¥Í≥† ÏïàÏ†ïÏ†Å)
          this.performInstantUserAuth();
          return; // getSession ÏôÑÏ†ÑÌûà Ïö∞Ìöå
        }
        
        // üöÄ Î°úÍ∑∏Ïù∏ ÌéòÏù¥ÏßÄ ÏÑ±Îä• ÏµúÏ†ÅÌôî: ÌÜ†ÌÅ∞Ïù¥ ÏóÜÏúºÎ©¥ Ï¶âÏãú Î°úÍ∑∏ÏïÑÏõÉ ÏÉÅÌÉú Ï≤òÎ¶¨
        if (!hasStoredToken && isLoginPage) {
          console.log('‚ö° [AuthStore] Î°úÍ∑∏Ïù∏ ÌéòÏù¥ÏßÄÏóêÏÑú ÌÜ†ÌÅ∞ ÏóÜÏùå ‚Üí Ï¶âÏãú Î°úÍ∑∏ÏïÑÏõÉ ÏÉÅÌÉú Ï≤òÎ¶¨ (getSession Í±¥ÎÑàÎõ∞Í∏∞)');
          this.updateState({
            session: null,
            user: null,
            userProfile: null,
            isAuthenticated: false,
            isLoading: false,
            isInitialized: true,
            signOut: this.signOut.bind(this),
            loadUserProfile: this.loadUserProfile.bind(this),
          });
          return;
        }
        
        // Ï†ÄÏû•Îêú ÌÜ†ÌÅ∞Ïù¥ ÏóÜÍ≥† ÏùºÎ∞ò ÌéòÏù¥ÏßÄÏóêÏÑúÎèÑ Îπ†Î•∏ Ï¥àÍ∏∞Ìôî
        if (!hasStoredToken && !isCallbackPage) {
          console.log('‚ö° [AuthStore] Ï†ÄÏû•Îêú ÌÜ†ÌÅ∞ ÏóÜÏùå ‚Üí Îπ†Î•∏ Î°úÍ∑∏ÏïÑÏõÉ ÏÉÅÌÉú Ï≤òÎ¶¨');
          this.updateState({
            session: null,
            user: null,
            userProfile: null,
            isAuthenticated: false,
            isLoading: false,
            isInitialized: true,
            signOut: this.signOut.bind(this),
            loadUserProfile: this.loadUserProfile.bind(this),
          });
          return;
        }

        this.supabaseClient = createBrowserSupabaseClient();
        this.initPromise = this.initialize();
      } catch (error) {
        console.error('‚ùå [AuthStore] Supabase ÌÅ¥ÎùºÏù¥Ïñ∏Ìä∏ ÏÉùÏÑ± Ïã§Ìå®:', error);
        
        // ÌÅ¥ÎùºÏù¥Ïñ∏Ìä∏ ÏÉùÏÑ± Ïã§Ìå® ÏãúÏóêÎèÑ Í∏∞Î≥∏ ÏÉÅÌÉúÎ°ú Ï¥àÍ∏∞Ìôî
        this.updateState({
          session: null,
          user: null,
          userProfile: null,
          isAuthenticated: false,
          isLoading: false,
          isInitialized: true,
          signOut: this.signOut.bind(this),
          loadUserProfile: this.loadUserProfile.bind(this),
        });
      }
    } else {
      // SSR ÌôòÍ≤ΩÏóêÏÑúÎäî Í∏∞Î≥∏ ÏÉÅÌÉúÎ°ú Ï¥àÍ∏∞Ìôî
      console.log('üåê [AuthStore] SSR ÌôòÍ≤ΩÏóêÏÑú Í∏∞Î≥∏ Ï¥àÍ∏∞Ìôî');
      this.updateState({
        session: null,
        user: null,
        userProfile: null,
        isAuthenticated: false,
        isLoading: false,
        isInitialized: true,
        signOut: this.signOut.bind(this),
        loadUserProfile: this.loadUserProfile.bind(this),
      });
    }
  }

  private async initialize() {
    if (!this.supabaseClient) {
      console.warn('‚ö†Ô∏è [AuthStore] Supabase ÌÅ¥ÎùºÏù¥Ïñ∏Ìä∏Í∞Ä ÏóÜÏñ¥ Ï¥àÍ∏∞ÌôîÎ•º Í±¥ÎÑàÎúÅÎãàÎã§.');
      this.updateState({
        ...this.state,
        isLoading: false,
        isInitialized: true,
      });
      return;
    }

    // Ïù¥ÎØ∏ Ï¥àÍ∏∞ÌôîÎêú Í≤ΩÏö∞ Ïû¨Ï¥àÍ∏∞Ìôî Î∞©ÏßÄ
    if (this.state.isInitialized) {
      console.log('‚úÖ [AuthStore] Ïù¥ÎØ∏ Ï¥àÍ∏∞ÌôîÎê® - Ïû¨Ï¥àÍ∏∞Ìôî Í±¥ÎÑàÎúÄ');
      return;
    }

    try {
      console.log('üöÄ [AuthStore] ÏôÑÏ†Ñ Ïø†ÌÇ§ Í∏∞Î∞ò Ï¥àÍ∏∞Ìôî ÏãúÏûë (ÎÑ§Ìä∏ÏõåÌÅ¨ ÏöîÏ≤≠ 0Í∞ú)');
      
      // üéØ ÏôÑÏ†ÑÌûà Ïø†ÌÇ§ Í∏∞Î∞ò: JWT ÌååÏã±Îßå ÏÇ¨Ïö©, getUser() Î∞è getSession() ÏôÑÏ†Ñ Ï†úÍ±∞
      await this.performInstantUserAuth();
      
      console.log('‚úÖ [AuthStore] Ïø†ÌÇ§ Í∏∞Î∞ò Ï¥àÍ∏∞Ìôî ÏôÑÎ£å (ÎÑ§Ìä∏ÏõåÌÅ¨ ÏöîÏ≤≠ ÏóÜÏùå)');
    } catch (error) {
      console.error('‚ùå [AuthStore] Ï¥àÍ∏∞Ìôî ÏóêÎü¨:', error);
      this.updateState({
        ...this.state,
        isLoading: false,
        isInitialized: true,
      });
    }
  }

  // updateAuthState Î©îÏÜåÎìú Ï†úÍ±∞Îê® - ÏôÑÏ†ÑÌûà Ïø†ÌÇ§ Í∏∞Î∞òÏúºÎ°ú Î≥ÄÍ≤Ω
  // Î™®Îì† Ïù∏Ï¶ù ÏÉÅÌÉúÎäî JWT ÌååÏã±ÏúºÎ°úÎßå Ï≤òÎ¶¨ÌïòÎ©∞ ÎÑ§Ìä∏ÏõåÌÅ¨ ÏöîÏ≤≠ ÏóÜÏùå

  private updateState(newState: AuthContextType) {
    const prevState = this.state;
    this.state = newState;
    
    // ÎîîÎ≤ÑÍπÖ: ÏÉÅÌÉú Î≥ÄÍ≤Ω Î°úÍ∑∏
    if (process.env.NODE_ENV === 'development') {
      console.log('üîÑ [AuthStore] ÏÉÅÌÉú Î≥ÄÍ≤Ω:', {
        Î≥ÄÍ≤ΩÏ†Ñ: {
          isAuthenticated: prevState.isAuthenticated,
          isLoading: prevState.isLoading,
          isInitialized: prevState.isInitialized,
          hasUser: !!prevState.user,
          hasSession: !!prevState.session
        },
        Î≥ÄÍ≤ΩÌõÑ: {
          isAuthenticated: newState.isAuthenticated,
          isLoading: newState.isLoading,
          isInitialized: newState.isInitialized,
          hasUser: !!newState.user,
          hasSession: !!newState.session
        },
        listenersÍ∞úÏàò: this.listeners.size
      });
    }
    
    this.listeners.forEach(listener => listener(newState));
  }

  public subscribe(listener: (state: AuthContextType) => void): () => void {
    this.listeners.add(listener);
    // Íµ¨ÎèÖ Ï¶âÏãú ÌòÑÏû¨ ÏÉÅÌÉú Ï†ÑÎã¨
    listener(this.state);
    
    return () => {
      this.listeners.delete(listener);
    };
  }

  public getState(): AuthContextType {
    return this.state;
  }

  public async waitForInitialization(): Promise<void> {
    if (this.initPromise) {
      await this.initPromise;
    }
  }

  private async signOut(): Promise<void> {
    if (!this.supabaseClient) return;

    try {
      console.log('üîÑ [AuthStore] Î°úÍ∑∏ÏïÑÏõÉ ÏãúÏûë');
      const { error } = await this.supabaseClient.auth.signOut();
      
      if (error) {
        console.error('‚ùå [AuthStore] Î°úÍ∑∏ÏïÑÏõÉ ÏóêÎü¨:', error);
      } else {
        console.log('‚úÖ [AuthStore] Î°úÍ∑∏ÏïÑÏõÉ ÏôÑÎ£å');
      }
    } catch (error) {
      console.error('‚ùå [AuthStore] Î°úÍ∑∏ÏïÑÏõÉ ÏòàÏô∏:', error);
    }
  }

  private async performInstantUserAuth(): Promise<void> {
    try {
      console.log('üöÄ [AuthStore] performInstantUserAuth ÏãúÏûë (ÎÑ§Ìä∏ÏõåÌÅ¨ ÏöîÏ≤≠ ÏóÜÏùå)');
      const startTime = performance.now();
      
      // üéØ Ïø†ÌÇ§ÏóêÏÑú Ï¶âÏãú JWT ÌååÏã± (ÎÑ§Ìä∏ÏõåÌÅ¨ ÏöîÏ≤≠ ÏóÜÏùå!)
      const { getInstantUserFromCookies, getTokenExpiry, isTokenExpiringSoon } = await import('@/utils/jwt-parser');
      
      const user = getInstantUserFromCookies();
      const tokenExpiry = getTokenExpiry();
      const expiringSoon = isTokenExpiringSoon();
      
      const endTime = performance.now();
      
      console.log('‚úÖ [AuthStore] JWT ÌååÏã± ÏôÑÎ£å:', {
        duration: `${(endTime - startTime).toFixed(2)}ms`,
        hasUser: !!user,
        userEmail: user?.email,
        userId: user?.id?.substring(0, 8) + '...',
        tokenExpiry: tokenExpiry?.toISOString(),
        expiringSoon
      });

      if (!user) {
        console.warn('‚ö†Ô∏è [AuthStore] Ïø†ÌÇ§ÏóêÏÑú Ïú†Ìö®Ìïú ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥ ÏóÜÏùå');
        
        // ÌÜ†ÌÅ∞Ïù¥ ÏóÜÍ±∞ÎÇò ÎßåÎ£åÎê®
        this.updateState({
          session: null,
          user: null,
          userProfile: null,
          isAuthenticated: false,
          isLoading: false,
          isInitialized: true,
          signOut: this.signOut.bind(this),
          loadUserProfile: this.loadUserProfile.bind(this),
        });
        return;
      }

      // ÏÇ¨Ïö©ÏûêÍ∞Ä ÏûàÏúºÎ©¥ Ï¶âÏãú Ïù∏Ï¶ùÎêú ÏÉÅÌÉúÎ°ú ÏÑ§Ï†ï
      console.log('‚úÖ [AuthStore] JWTÏóêÏÑú ÏÇ¨Ïö©Ïûê ÌôïÏù∏ ÏÑ±Í≥µ:', {
        userId: user.id?.substring(0, 8) + '...',
        email: user.email,
        provider: user.app_metadata?.provider,
        createdAt: user.created_at
      });

      // ÏÑ∏ÏÖò Í∞ùÏ≤¥ ÏÉùÏÑ± (JWT Í∏∞Î∞ò)
      const instantSession = {
        user: user,
        access_token: 'token-from-jwt', // Ïã§Ï†ú ÌÜ†ÌÅ∞ÏùÄ JWTÏóêÏÑú ÌååÏã±Îê®
        refresh_token: null,
        expires_at: tokenExpiry ? Math.floor(tokenExpiry.getTime() / 1000) : null,
        token_type: 'bearer'
      };

      console.log('üîÑ [AuthStore] Ïù∏Ï¶ù ÏÉÅÌÉú ÏóÖÎç∞Ïù¥Ìä∏ Ï§ë...');
      this.updateState({
        user: user,
        session: instantSession as any,
        userProfile: null,
        isLoading: false, // Ï¶âÏãú Î°úÎî© ÏôÑÎ£å
        isInitialized: true,
        isAuthenticated: true,
        signOut: this.signOut.bind(this),
        loadUserProfile: this.loadUserProfile.bind(this),
      });
      
      console.log('üéâ [AuthStore] Ïù∏Ï¶ù ÏÉÅÌÉú ÏóÖÎç∞Ïù¥Ìä∏ ÏôÑÎ£å - Î°úÎî© Ìï¥Ï†úÎê® (JWT Î∞©Ïãù)');

      // üîß Í∞úÎ∞ú ÌôòÍ≤ΩÏóêÏÑú userProfile Î°úÎî© ÏãúÍ∞Ñ Ï∂îÏ†Å
      if (process.env.NODE_ENV === 'development') {
        (window as any).authStartTime = Date.now();
      }

      // ÌîÑÎ°úÌïÑ Ï∫êÏã± Î°úÏßÅ: ÌîÑÎ°úÌïÑÏù¥ ÏóÜÍ±∞ÎÇò ÏÇ¨Ïö©ÏûêÍ∞Ä Î≥ÄÍ≤ΩÎêú Í≤ΩÏö∞ÏóêÎßå Î°úÎìú
      const shouldLoadProfile = !this.state.userProfile || 
                               (this.state.userProfile?.id !== user.id);
      
      if (shouldLoadProfile) {
        console.log('üîÑ [AuthStore] ÏÇ¨Ïö©Ïûê ÌîÑÎ°úÌïÑ Î°úÎìú ÏãúÏûë:', {
          userId: user.id?.substring(0, 8) + '...',
          hasUserId: !!user.id,
          userEmail: user.email,
          reason: !this.state.userProfile ? 'profile_not_cached' : 'user_changed',
          previousUserId: this.state.userProfile?.id?.substring(0, 8) + '...' || 'none'
        });
        
        this.loadUserProfile(user.id).then(profile => {
          if (profile) {
            console.log('‚úÖ [AuthStore] ÏÇ¨Ïö©Ïûê ÌîÑÎ°úÌïÑ Î°úÎìú ÏÑ±Í≥µ:', {
              is_admin: profile.is_admin,
              is_super_admin: profile.is_super_admin
            });
            this.updateState({
              ...this.state,
              userProfile: profile,
            });
          } else {
            console.warn('‚ö†Ô∏è [AuthStore] ÏÇ¨Ïö©Ïûê ÌîÑÎ°úÌïÑ Î°úÎìú Í≤∞Í≥ºÍ∞Ä nullÏûÑ');
          }
        }).catch(error => {
          console.warn('‚ö†Ô∏è [AuthStore] ÏÇ¨Ïö©Ïûê ÌîÑÎ°úÌïÑ Î°úÎìú Ïã§Ìå®:', error);
        });
      } else {
          console.log('‚úÖ [AuthStore] ÎèôÏùº ÏÇ¨Ïö©Ïûê ÌîÑÎ°úÌïÑÏù¥ Ïù¥ÎØ∏ Ï∫êÏãúÎê® - Ïû¨Î°úÎî© Í±¥ÎÑàÎúÄ:', {
            userId: user.id?.substring(0, 8) + '...',
            cachedProfile: {
              nickname: this.state.userProfile?.nickname,
              is_admin: this.state.userProfile?.is_admin
            }
          });
        }

      // ÌÜ†ÌÅ∞ ÎßåÎ£å Í≤ΩÍ≥† (Ïø†ÌÇ§ Í∏∞Î∞ò)
      if (expiringSoon) {
        console.warn('‚ö†Ô∏è [AuthStore] ÌÜ†ÌÅ∞Ïù¥ Í≥ß ÎßåÎ£åÎê® (30Î∂Ñ Ïù¥ÎÇ¥) - Ïû¨Î°úÍ∑∏Ïù∏ ÌïÑÏöîÌï† Ïàò ÏûàÏùå');
        // Î∞±Í∑∏ÎùºÏö¥Îìú ÎÑ§Ìä∏ÏõåÌÅ¨ ÏöîÏ≤≠ ÏóÜÏù¥ Í≤ΩÍ≥†Îßå ÌëúÏãú
      }

      // Ïù∏Ï¶ù ÏÉÅÌÉú Î≥ÄÍ≤Ω Î¶¨Ïä§ÎÑà Îì±Î°ù (Ïø†ÌÇ§ Í∏∞Î∞ò Î™®Îìú)
      this.supabaseClient.auth.onAuthStateChange(async (event: string, session: any) => {
        // Î°úÍ∑∏ÏïÑÏõÉ Ïù¥Î≤§Ìä∏Îßå Ï≤òÎ¶¨ (Îã§Î•∏ Ïù¥Î≤§Ìä∏Îäî Ïø†ÌÇ§ Í∏∞Î∞òÏúºÎ°ú Ïù¥ÎØ∏ Ï≤òÎ¶¨Îê®)
        if (event === 'SIGNED_OUT' || !session) {
          console.log('üö™ [AuthStore] Î°úÍ∑∏ÏïÑÏõÉ Ïù¥Î≤§Ìä∏ - ÏÉÅÌÉú Ï†ïÎ¶¨');
          this.updateState({
            session: null,
            user: null,
            userProfile: null,
            isAuthenticated: false,
            isLoading: false,
            isInitialized: true,
            signOut: this.signOut.bind(this),
            loadUserProfile: this.loadUserProfile.bind(this),
          });
        }
        // Îã§Î•∏ Ïù¥Î≤§Ìä∏(SIGNED_IN, TOKEN_REFRESHED Îì±)Îäî Ï°∞Ïö©Ìûà Î¨¥Ïãú
        // Ïø†ÌÇ§ Í∏∞Î∞òÏúºÎ°ú Ïù¥ÎØ∏ Ï≤òÎ¶¨ÎêòÎØÄÎ°ú Ï∂îÍ∞Ä Î°úÍ∑∏ ÏóÜÏù¥ Î¨¥Ïãú
      });

    } catch (error) {
      console.error('‚ùå [AuthStore] performInstantUserAuth ÏòàÏô∏:', {
        error: error instanceof Error ? error.message : String(error),
        stack: error instanceof Error ? error.stack?.substring(0, 200) : undefined
      });
      
      // Ïò§Î•ò Î∞úÏÉùÏãú ÎπÑÏù∏Ï¶ù ÏÉÅÌÉúÎ°ú ÏÑ§Ï†ï
      this.updateState({
        session: null,
        user: null,
        userProfile: null,
        isAuthenticated: false,
        isLoading: false,
        isInitialized: true,
        signOut: this.signOut.bind(this),
        loadUserProfile: this.loadUserProfile.bind(this),
      });
      
      console.log('üîÑ [AuthStore] Ïò§Î•òÎ°ú Ïù∏Ìïú ÎπÑÏù∏Ï¶ù ÏÉÅÌÉú ÏÑ§Ï†ï ÏôÑÎ£å');
    }
  }



  private async checkTokenStatusFromCookies(): Promise<void> {
    try {
      console.log('üîÑ [AuthStore] Ïø†ÌÇ§ Í∏∞Î∞ò ÌÜ†ÌÅ∞ ÏÉÅÌÉú Ï≤¥ÌÅ¨');
      
      // ÏôÑÏ†ÑÌûà Ïø†ÌÇ§ Í∏∞Î∞ò - ÎÑ§Ìä∏ÏõåÌÅ¨ ÏöîÏ≤≠ ÏóÜÏùå
      const { getInstantUserFromCookies, getTokenExpiry } = await import('@/utils/jwt-parser');
      
      const user = getInstantUserFromCookies();
      const tokenExpiry = getTokenExpiry();
      
      if (!user) {
        console.warn('‚ö†Ô∏è [AuthStore] Ïø†ÌÇ§ÏóêÏÑú Ïú†Ìö®Ìïú ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥ ÏóÜÏùå - Î°úÍ∑∏ÏïÑÏõÉ Ï≤òÎ¶¨');
        this.updateState({
          ...this.state,
          session: null,
          user: null,
          userProfile: null,
          isAuthenticated: false,
        });
        return;
      }

      // ÌÜ†ÌÅ∞ ÎßåÎ£å Ï≤¥ÌÅ¨ (ÌÅ¥ÎùºÏù¥Ïñ∏Ìä∏ ÏÇ¨Ïù¥Îìú)
      if (tokenExpiry && tokenExpiry <= new Date()) {
        console.warn('‚ö†Ô∏è [AuthStore] JWT ÌÜ†ÌÅ∞Ïù¥ ÎßåÎ£åÎê® - Î°úÍ∑∏ÏïÑÏõÉ Ï≤òÎ¶¨');
        this.updateState({
          ...this.state,
          session: null,
          user: null,
          userProfile: null,
          isAuthenticated: false,
        });
        return;
      }

      console.log('‚úÖ [AuthStore] Ïø†ÌÇ§ Í∏∞Î∞ò ÌÜ†ÌÅ∞ ÏÉÅÌÉú Ï≤¥ÌÅ¨ ÏôÑÎ£å - Ïú†Ìö®Ìï®');
    } catch (error) {
      console.warn('‚ö†Ô∏è [AuthStore] Ïø†ÌÇ§ Í∏∞Î∞ò ÌÜ†ÌÅ∞ Ï≤¥ÌÅ¨ Ï§ë Ïò§Î•ò:', error);
    }
  }

  private async loadUserProfile(userId: string): Promise<UserProfiles | null> {
    try {
      console.log('üîç [AuthStore] APIÎ•º ÌÜµÌïú ÌîÑÎ°úÌïÑ Ï°∞Ìöå ÏãúÏûë:', { userId: userId.substring(0, 8) + '...' });
      
      // üöÄ ÏÑúÎ≤Ñ APIÎ•º ÌÜµÌï¥ ÌîÑÎ°úÌïÑ Ï°∞Ìöå (RLS Ï†ïÏ±Ö Ïö∞Ìöå)
      const response = await fetch('/api/user/profile', {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
        },
        credentials: 'include', // Ïø†ÌÇ§ Ìè¨Ìï®
      });

      if (!response.ok) {
        const errorData = await response.json();
        console.warn('‚ö†Ô∏è [AuthStore] API ÌîÑÎ°úÌïÑ Ï°∞Ìöå Ïã§Ìå®:', {
          status: response.status,
          statusText: response.statusText,
          error: errorData
        });
        
        // 404ÎÇò 403 ÏóêÎü¨Ïù∏ Í≤ΩÏö∞ null Î∞òÌôò (ÌîÑÎ°úÌïÑ ÏóÜÏùå)
        if (response.status === 404 || response.status === 403) {
          return null;
        }
        
        throw new Error(`API ÏùëÎãµ Ïã§Ìå®: ${response.status} ${response.statusText}`);
      }

      const data = await response.json();
      
      if (!data.success || !data.user) {
        console.warn('‚ö†Ô∏è [AuthStore] API ÏùëÎãµÏóêÏÑú ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥ ÏóÜÏùå:', data);
        return null;
      }

      // API ÏùëÎãµÏùÑ UserProfiles ÌòïÏãùÏúºÎ°ú Î≥ÄÌôò
      const userProfile: UserProfiles = {
        id: data.user.id,
        email: data.user.email,
        nickname: data.user.name,
        avatar_url: data.user.avatar_url,
        star_candy: data.user.star_candy || 0,
        star_candy_bonus: data.user.star_candy_bonus || 0,
        is_admin: data.user.is_admin || false,
        is_super_admin: data.user.is_super_admin || false,
        created_at: data.user.created_at,
        updated_at: data.user.updated_at,
        // Í∏∞Î≥∏Í∞í ÏÑ§Ï†ï
        birth_date: null,
        birth_time: null,
        deleted_at: null,
        gender: null,
        open_ages: false,
        open_gender: false
      };

      console.log('‚úÖ [AuthStore] APIÎ•º ÌÜµÌïú ÌîÑÎ°úÌïÑ Ï°∞Ìöå ÏÑ±Í≥µ:', {
        id: userProfile.id?.substring(0, 8) + '...',
        nickname: userProfile.nickname,
        email: userProfile.email,
        hasAvatar: !!userProfile.avatar_url,
        is_admin: userProfile.is_admin,
        is_super_admin: userProfile.is_super_admin,
        star_candy: userProfile.star_candy
      });

      return userProfile;

    } catch (error) {
      console.error('‚ùå [AuthStore] API ÌîÑÎ°úÌïÑ Ï°∞Ìöå ÏòàÏô∏:', error);
      
      // API Ìò∏Ï∂ú Ïã§Ìå®Ïãú fallbackÏúºÎ°ú Í∏∞Î≥∏ ÌîÑÎ°úÌïÑ Î∞òÌôò (Í∞úÎ∞ú ÌôòÍ≤Ω)
      if (process.env.NODE_ENV === 'development') {
        console.log('üîß [AuthStore] Í∞úÎ∞úÌôòÍ≤Ω - Í∏∞Î≥∏ ÌîÑÎ°úÌïÑ fallback');
        
        // ÌòÑÏû¨ ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥ Í∞ÄÏ†∏Ïò§Í∏∞
        const { data: { user: currentUser } } = await this.supabaseClient?.auth.getUser() || { data: { user: null } };
        
        if (currentUser && currentUser.id === userId) {
          const fallbackProfile: UserProfiles = {
            id: userId,
            email: currentUser.email || null,
            nickname: currentUser.user_metadata?.name || 
                     currentUser.user_metadata?.full_name || 
                     currentUser.email?.split('@')[0] || 
                     'User',
            avatar_url: null, // JWT ÌÜ†ÌÅ∞ Ïù¥ÎØ∏ÏßÄÎäî ÏÇ¨Ïö©ÌïòÏßÄ ÏïäÏùå
            is_admin: true, // Í∞úÎ∞úÌôòÍ≤ΩÏóêÏÑú API Ïã§Ìå®Ïãú ÏûÑÏãú Í¥ÄÎ¶¨Ïûê
            is_super_admin: false,
            star_candy: 0,
            star_candy_bonus: 0,
            created_at: new Date().toISOString(),
            updated_at: new Date().toISOString(),
            birth_date: null,
            birth_time: null,
            deleted_at: null,
            gender: null,
            open_ages: false,
            open_gender: false
          };
          
          console.log('üêõ [AuthStore] Í∞úÎ∞úÌôòÍ≤Ω fallback ÌîÑÎ°úÌïÑ ÏÉùÏÑ±:', {
            nickname: fallbackProfile.nickname,
            is_admin: fallbackProfile.is_admin
          });
          
          return fallbackProfile;
        }
      }
      
      return null;
    }
  }
}

// AuthProvider Ïª¥Ìè¨ÎÑåÌä∏Î•º memoÎ°ú Í∞êÏã∏ÏÑú ÏôÑÏ†ÑÌûà ÏïàÏ†ïÌôî
const AuthProviderComponent = memo(function AuthProviderInternal({ children }: AuthProviderProps) {
  console.log('üèóÔ∏è [AuthProvider] Ïª¥Ìè¨ÎÑåÌä∏ ÏÉùÏÑ±/Ïû¨Î†åÎçîÎßÅ');
  
  const [contextValue, setContextValue] = useState<AuthContextType>(() => {
    return AuthStore.getInstance().getState();
  });

  useEffect(() => {
    const authStore = AuthStore.getInstance();
    
    // Ï¥àÍ∏∞Ìôî ÎåÄÍ∏∞ (await Ï∂îÍ∞Ä)
    const initializeAndSubscribe = async () => {
      try {
        await authStore.waitForInitialization();
        console.log('‚úÖ [AuthProvider] Ï¥àÍ∏∞Ìôî ÏôÑÎ£å ÎåÄÍ∏∞ ÏÑ±Í≥µ');
      } catch (error) {
        console.error('‚ùå [AuthProvider] Ï¥àÍ∏∞Ìôî ÎåÄÍ∏∞ Ï§ë Ïò§Î•ò:', error);
      }
    };
    
    initializeAndSubscribe();
    
    // ÏÉÅÌÉú Î≥ÄÍ≤Ω Íµ¨ÎèÖ
    const unsubscribe = authStore.subscribe((newState) => {
      console.log('üîÑ [AuthProvider] Context Í∞í Î≥ÄÍ≤Ω:', {
        isLoading: newState.isLoading,
        isInitialized: newState.isInitialized,
        isAuthenticated: newState.isAuthenticated,
        hasSession: !!newState.session,
        hasUser: !!newState.user,
        hasUserProfile: !!newState.userProfile,
      });
      setContextValue(newState);
    });

    return unsubscribe;
  }, []);

  return (
    <AuthContext.Provider value={contextValue}>
      {children}
    </AuthContext.Provider>
  );
});

// AuthProviderÎ•º ÏôÑÏ†ÑÌûà ÏïàÏ†ïÌôîÎêú Ïª¥Ìè¨ÎÑåÌä∏Î°ú export
export const AuthProvider = AuthProviderComponent;

export function useAuth(): AuthContextType {
  const context = useContext(AuthContext);
  if (context === undefined) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  return context;
} 